*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
 }
 body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: scroll;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* Add a black background color to the top navigation */
.topnav {
  background-color: rgb(255, 255, 255);
  overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
  margin-right: 30px;
  float: right;
  color: #ffffff;
  text-align: center;
  padding: 16px 18px;
  text-decoration: none;
  font-size: 20px;
}

/* Change the color of links on hover */
.topnav a:hover {
  text-decoration: wavy;
  color: #fde45a;
  border-radius: 5px;
}

.about {
  background-color: #0b0a0f;
}
.ct {
  text-align: center;
  margin-top: 350px;
  color: white;
  text-transform: uppercase;
  font-family: 'Rubik Moonrocks', cursive;
  font-size: 50px;
  height: 75px;
}
.ct1 {
  text-align: center;
  margin-top: 50px;
  color: white;
  font-family: 'Open Sans', sans-serif;
}
.ct2 {
  text-align: right;
  padding: 300px 120px;
  color: white;
  font-family: 'Secular One', sans-serif;
  font-size: 50px;
}
.ct3 {
  text-align: right;
  padding: 120px;
  margin-top: -400px;
  align-content: center;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
}
button {
  border: none;
  padding: 15px 32px;
  color: #0b0a0f;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 10px;
  font-family: 'Open Sans', sans-serif;
}
button:hover {
  background-color: #fde45a ;
}
.ct4 {
  padding-left: 1650px;
  padding-top: -100px;
}
.contact {
  background-color: #fd891c;
}
.ct5 {
  text-align: left;
  padding: 300px 120px;
  color: white;
  font-family: 'Secular One', sans-serif;
  font-size: 50px;
}
.ct6 {
  text-align: left;
  padding: 120px;
  margin-top: -400px;
  align-content: center;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
}
.ct7 {
  padding-left: 120px;
  padding-bottom: -20px;
}
footer {
  height: 30px;
  text-align: center;
  align-items: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  margin: 4px 2px;
}
a {
  color: #0b0a0f;
  text-decoration: none;
}

a:hover {
  text-decoration: wavy;
  color: #fde45a;
  cursor:pointer;  
}

/* Use a media query to add a breakpoint at 800px: */
@media screen and (max-width: 800px) {
  .topnav .topnav a .about .ct .ct1 .ct2 .ct3 .ct4 .ct5 .ct6 .ct7 button .contact footer{
    display:flex;
  }
}
@media only screen and (min-width: 1024px) {
  .topnav .topnav a .about .ct .ct1 .ct2 .ct3 .ct4 .ct5 .ct6 .ct7 button .contact footer{
    display:flex;
  }
}